.accordion {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  margin-bottom: 3rem;

  &__item {
    position: relative;
    max-width: 119rem;
    padding: 2rem 0 2rem 1rem;
    border: 0.1rem solid var(--gray_4);
    border-radius: 0.5rem;
  
    @media (min-width: 500px) {
      padding: 2rem 0 2rem 3rem;
    }
  
    @media (min-width: 1024px) {
      padding: 2rem 3rem;
      margin-bottom: 3rem;
    }
  }

  h3 {
    position: relative;
    margin: 0;
    text-align: left;
    padding-left: 1.5rem;
    font-size: 1.6rem;
    line-height: 2rem;
    color: #000;

    @media (min-width: 1024px) {
      padding-left: 0;
      font-size: 2.4rem;
      line-height: 2.8rem;
    }

  // Крестик, стрелочка или что-то иное меняющееся при открытии аккардиона
    &::after {
      position: absolute;
      content: "";
      transform: translateY(-50%) rotate(1deg);
      width: 1.2rem;
      height: 1rem;
      background: url("../img/icons/arrow.svg") 100% 100%/1.2rem 1rem no-repeat;
      will-change: transform;
      transition: transform .5s linear;

      @media (min-width: 500px) {
        width: 2.7rem;
        height: 2.7rem;
      }

      @media (min-width: 1024px) {
        right: 0;
      }
    }
  }
}

.accordion__item.active {
  border: none;
  background: var(--white_2);
}

.accordion__item.active h3 {
  margin-bottom: 2.1rem;
}

.accordion__item.active h3::after {
  right: -1.1rem;
  transform: translateY(-50%) rotate(180deg) !important;
}

// Текст в блоках аккардиона
.accordion__content {
  width: 100%;
  height: 0;
  overflow: hidden;
  opacity: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  font-size: 1.4rem;
  line-height: 1.8rem;
  color: #000;
  will-change: auto;
  transition: all .5s ease-out;

  @media (min-width: 1024px) {
    font-size: 2.2rem;
    line-height: 2.6rem;
  }

  img {
    height: 25.2rem;
    // align-self: center;
  }

  p {
    max-width: 53rem;
    margin-right: 2rem;
  }
}

.accordion__content.active {
  opacity: 1;
}
